.MiningProfileListItem {
  position: relative;
  width: 100%;
  border-radius: 10px;
  background-color: rgba(#344dce, 0.2);
  display: flex;
  align-items: center;
  padding: 15px;
  gap: 15px;
  img {
    width: 25%;
  }
  overflow: hidden;
}

.contentWrap {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 15px;
}

.durationTag {
  width: fit-content;
  padding: 10px;
  background-color: #1e2651;
  border-radius: 10px;
  h2 {
    font-size: 14px;
    line-height: 20px;
  }
}
.tagWrap {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 10px;
}

.valueWrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.valueItem {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  .title,
  .value {
    font-weight: 900;
    font-size: 16px;
    margin: 0;
  }
  .value {
    color: white;
  }
}
.buttonWrap {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 10px;
  .button {
    width: 50%;
    min-width: none;
    // padding: 0 10px;
  }
}

@media (max-width: 765px) {
  .valueItem {
    .title,
    .value {
      font-size: 14px;
    }
  }
}
