.MiningProfile {
  position: relative;
  width: 100%;
  border: 2px solid rgba(#344dce, 0.4);
  border-radius: 20px;
  overflow: hidden;
}
.header {
  background-color: rgba(#344dce, 0.2);
  padding: 10px 0;
  display: flex;
  justify-content: center;
}
hr {
  border: none;
  border-top: 2px solid rgba(#344dce, 0.4);
  margin: 0;
}
.stats {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  padding: 25px 0;
  .statsItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    h2 {
      font-size: 14px;
    }
    p {
      margin: 0;
      color: white;
      font-size: 20px;
      font-weight: 900;
      span {
        font-weight: 900;
        margin-left: 5px;
      }
    }
    .unit {
      font-size: 16px;
    }
    .button {
      height: 40px;
    }
  }
}

.miningList {
  width: 100%;
  padding: 25px;
}

@media (max-width: 765px) {
  // .MiningProfile{

  // }
  .stats {
    .statsItem {
      align-items: center;
      p {
        font-size: 16px;
      }
    }
  }
}
