@use "../../../variables" as var;
.MiningNFTAction {
  position: relative;
  width: 100%;
  text-align: left;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  row-gap: 20px;
  overflow: hidden;
  .nftSelectorWrap,
  .durationSelectorWrap {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 15px;
  }
  .subtitle {
    font-weight: 700;
  }
  .selectorList {
    width: 100%;
  }
  .nftSelectorListItem {
    position: absolute;
    width: 100%;
    height: 100%;
    cursor: pointer;
    background: rgba(85, 66, 193, 0.5);
    border: 2px solid #5542c1;
    border-radius: 10px;

    &.active,
    &:hover {
      background: #4c5eff;
      border-color: transparent;
    }
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      height: 80%;
    }
  }
  .durationSelectorListItem {
    width: 100%;
    cursor: pointer;
    background: rgba(85, 66, 193, 0.5);
    border: 2px solid #5542c1;
    border-radius: 10px;
    &.active,
    &:hover {
      background: #4c5eff;
      border-color: transparent;
      color: white;
    }
    font-weight: 700;
    font-size: 16px;
    text-align: center;
    color: var.$color-primary;
    padding: 10px 0;
  }
  .nftPrice {
    color: var.$color-primary;
    text-align: center;
    margin-top: 10px;
    font-size: 16px;
    line-height: 18px;
    font-weight: 700;
    margin-bottom: 0;
  }
}

@media (max-width: 765px) {
  .MiningNFTAction {
    align-items: center;
    .durationSelectorListItem {
      font-size: 14px;
    }
    .nftPrice {
      word-spacing: 100vw;
      font-size: 14px;
      line-height: 16px;
    }
  }
}
