@use "../../../variables" as var;
.MiningLeaderboard {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  overflow: hidden;
}

.titleWrap {
  display: flex;
  align-items: center;
  column-gap: 15px;
  img {
    width: 50px;
  }
}

.listWrap {
  height: 100%;
  width: 100%;
  border-radius: 20px;
  background: rgba(#293263, 0.2);
  padding: 30px;
  .listTitle {
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      color: rgba(white, 0.2);
      margin: 0;
      font-size: 20px;
    }
  }
  .list {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 5px;
    .listItem {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      p {
        margin: 0;
      }
      color: white;
      div {
        display: flex;
        align-items: center;
        column-gap: 15px;
      }
      .ordinal,
      .address {
        font-weight: 700;
        font-size: 18px;
      }
      .ordinal {
        color: #fc48f8;
        min-width: 25px;
      }
      .address {
        color: var.$color-content-text;
      }
      .reward {
        font-size: 18px;
        color: white;
      }
    }
  }
}
