@use "./variables" as var;
.gradientTitle {
  font-weight: 900;
  font-size: 36px;
  line-height: 42px;
  // text-transform: uppercase;
  background: var.$color-title-text;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0;
}
.mainText {
  color: var.$color-content-text;
  font-size: 20px;
  line-height: 24px;
  // text-transform: capitalize;
  margin: 0;
}
.square {
  position: relative;
  // margin: 5px;
  // width: 100%;
  &:after {
    content: "";
    display: block;
    padding-bottom: 100%;
  }
}

@media (max-width: 765px) {
  .gradientTitle {
    font-size: 30px;
    line-height: 46px;
  }
  .mainText {
    font-size: 16px;
    line-height: 20px;
  }
}
