@use "../../variables" as var;
.About {
  position: relative;
  width: 100%;
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: transparent;
  overflow-x: hidden;
}
.themeImageWrap {
  overflow: hidden;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 90vh;
  img {
    width: 95%;
    max-width: 1400px;
  }
}
.mainContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 50px;
  padding-top: 50px;
}

.introWrap {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  // .text {
  //   width: 50%;
  // }
  img {
    width: 350px;
  }
}
.featureWrap {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 20px;
  .text {
    // width: 50%;
  }
  img {
    // width: 25%;
    width: 300px;
  }
}
.supportWrap {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: space-between;
  gap: 30px;
  .text {
    width: 40%;
    align-items: center;
    h2 {
      margin: 0;
    }
  }
  .imageWrap {
    width: 40%;
  }
  // .supportList {
  //   width: 100%;
  //   .supportListItem {
  //     width: 70%;
  //   }
  // }
}

