@media (max-width: 765px) {
  .themeImageWrap {
    height: unset;
    img {
      width: 130%;
    }
  }
  .mainContent {
    padding-top: 0;
  }

  .introWrap {
    flex-direction: column;
    justify-content: flex-start;
  }
  .featureWrap {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;

    img {
      width: 300px;
    }
  }
  .referralWrap {
    .titleWrap {
      flex-direction: column;
      align-items: center;
      gap: 10px;
      .title {
        align-items: center;
      }
    }
  }

  .supportWrap {
    flex-direction: column;

    .imageWrap,
    .text {
      width: 80%;
    }
  }

