@use "../../variables" as var;

.Header {
  position: relative;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px;
  .menuIcon {
    color: white;
    font-size: 24px;
    cursor: pointer;
  }
}

.iconWrap {
  height: 70%;
  display: flex;
  align-items: center;
  gap: 10px;
  img {
    height: 100%;
  }
}

.navWrap {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  gap: 25px;
  .navLink {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: white;
    &.active {
      color: var.$color-primary;
    }
  }
}

.walletWrap {
  display: flex;
  align-items: center;
  gap: 10px;
  img {
    height: 30px;
    // margin-top: -5px;
  }
  .langButton {
    > img {
      // height: 100%;
      width: 30px;
      height: auto;
    }
    :hover {
      cursor: pointer;
    }
  }
}
.menu {
  li:hover {
    font-weight: 600;
  }
}

@media (max-width: 765px) {
  .Header {
    padding: 0 10px;
  }
  .iconWrap {
    height: 60%;
    img {
      height: 100%;
    }
  }
  .navWrap {
    box-sizing: border-box;
    position: absolute;
    top: 60px;
    left: 0;
    transform: none;
    width: 100%;
    height: fit-content;
    z-index: 1;
    background-color: rgba($color: black, $alpha: 0.7);
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    row-gap: 10px;
    padding: 40px;
  }
  .walletWrap {
    img {
      height: 24px;
    }
    .walletButton {
      height: 35px;
    }
  }
  .mobileLangButton {
    // position: absolute;
    // right: 20px;
    > img {
      width: 35px;
      // height: 40px;
    }
  }
}
