@use "../../../variables" as var;
.MiningProfile {
  position: relative;
  width: 100%;
  border: 2px solid rgba(#344dce, 0.4);
  border-radius: 20px;
  overflow: hidden;
}
.header {
  background-color: rgba(#344dce, 0.2);
  padding: 10px 0;
  display: flex;
  justify-content: center;
}
hr {
  border: none;
  border-top: 2px solid rgba(#344dce, 0.4);
  margin: 0;
}
.stats {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  padding: 15px 0;
  .statsItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    h2 {
      font-size: 14px;
    }
    p {
      margin: 0;
      color: white;
      font-size: 20px;
      font-weight: 900;
      span {
        font-weight: 900;
        margin-left: 5px;
      }
    }
    .unit {
      font-size: 16px;
    }
    .button {
      height: 40px;
    }
  }
}

.referralLinkWrap {
  display: flex;
  align-items: stretch;
  gap: 20px;
  overflow: hidden;
  justify-content: center;
  padding: 10px;
  margin-top: 15px;
  .referralLink {
    height: fit-content;
    border-radius: 8px;
    border: 1px solid white;
    color: white;
    font-weight: 700;
    font-size: 20px;
    padding: 5px 10px;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */

    &::-webkit-scrollbar {
      display: none;
    }
  }
}
.note {
  margin: 15px 0;
  text-align: center;
  color: var.$color-content-text;
  font-size: 26px;
  font-weight: 700;
}

.referralList {
  width: 100%;
  // max-width: 800px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  padding: 15px;
  .referralListItem {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    p {
      margin: 0;
    }
    color: white;
    div {
      display: flex;
      align-items: center;
      column-gap: 15px;
    }

    .ordinal,
    .address,
    .reward {
      color: var.$color-content-text;
    }
    .ordinal,
    .address {
      font-weight: 700;
      font-size: 18px;
    }
    .ordinal {
      // color: #fc48f8;
      min-width: 25px;
    }
    .address {
      // color: var.$color-content-text;
    }
    .reward {
      font-size: 18px;
      // color: white;
    }
  }
}

@media (max-width: 765px) {
  // .MiningProfile{

  // }
  .stats {
    .statsItem {
      align-items: center;
      p {
        font-size: 16px;
      }
    }
  }
}

