@use "../../../variables" as var;
.Button {
  position: relative;
  min-width: 60px;
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 700;
  height: 45px;
  padding: 0 10px;
  background-color: var.$color-primary;
  color: white;
  border-radius: 10px;
  cursor: pointer;
  user-select: none;
  white-space: nowrap;
}
.gradient {
  background: var.$color-title-text;
  border: none;
  color: white;
}
.secondary {
  background-color: transparent;
  border: 2px solid #4c5eff;
  color: #fc48f8;
}
.disabled {
  // background-color: rgba(255, 255, 255, 0.2);
  // border: none;
  // color: white;
  opacity: 0.4;
  // pointer-events: none;
}

@media (max-width: 765px) {
  .Button {
    font-size: 14px;
    height: 40px;
  }
}
